html, body{
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent;
    &:hover{
        background: none;
        -webkit-tap-highlight-color: rgba(0,0,0,0);
        -webkit-tap-highlight-color: transparent;
    }
    &:focus{
        background: none;
        -webkit-tap-highlight-color: rgba(0,0,0,0);
        -webkit-tap-highlight-color: transparent;
    }
}
aside, nav, footer, header, section, article { display: block }
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
textarea{
    resize:none;
}
input, textarea { outline:none; }
input:active, textarea:active { outline:none; }
:focus { outline:none; }
body {
    margin:0;
    color:#000;
    font-family: 'FuturaPTLight';
    font-size:13px;
    background:#fff;
    min-width:320px;
    height:100%;
    position:relative;
    -webkit-text-size-adjust: none;
}
*{
    box-sizing:border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing:border-box;
    -o-box-sizing:border-box;
    -ms-box-sizing:border-box;
}
@mixin after() {
    content:'';
    display:block;
    clear:both;
}
@mixin transition() {
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}
#wrapper{
    overflow:hidden;
}
.container{
    max-width: 1170px;
    margin:0 auto;
    padding: 0 15px;
}
.header{
    min-height: 62px;
    background-color: #fff;
    position: fixed;
    z-index:10;
    width: 100%;
    top:0;
    left: 0;
    -webkit-box-shadow: 0 0 55px rgba(43, 43, 43, 0.34);
    -moz-box-shadow: 0 0 55px rgba(43, 43, 43, 0.34);
    box-shadow: 0 0 55px rgba(43, 43, 43, 0.34);
    .container{
        min-height: 62px;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        position: relative;
        .logo-block{
            position: absolute;
            top:0;
            left:15px;
            background-color: #fff;
            width: 195px;
            height:165px;
            display: flex;
            align-items: center;
            justify-content: center;
            -webkit-box-shadow: 0 0 55px rgba(43, 43, 43, 0.34);
            -moz-box-shadow: 0 0 55px rgba(43, 43, 43, 0.34);
            box-shadow: 0 0 55px rgba(43, 43, 43, 0.34);
            -webkit-border-bottom-right-radius: 5px;
            -webkit-border-bottom-left-radius: 5px;
            -moz-border-radius-bottomright: 5px;
            -moz-border-radius-bottomleft: 5px;
            border-bottom-right-radius: 5px;
            border-bottom-left-radius: 5px;
            &:after{
                @include after;
                position: absolute;
                top:0;
                left: -55px;
                width: 55px;
                height:62px;
                background-color: #fff;
            }
            &:before{
                @include after;
                position: absolute;
                top:0;
                right: -55px;
                width: 55px;
                height:62px;
                background-color: #fff;
            }
            .logo{
                display: block;
                background: url(../img/logo.png) no-repeat;
                width: 153px;
                height:153px;
                -webkit-background-size: 100% 100%;
                background-size: 100% 100%;
                a{
                    display: block;
                    height:100%;
                }
            }
        }
        .header-block{
            display: flex;
            flex-wrap: wrap;
            align-items: center;
        }
    }
}
.fixed{
    .container{
        .logo-block {
            height:100% !important;
            -webkit-box-shadow: none;
            -moz-box-shadow: none;
            box-shadow: none;
            width: auto !important;
            background-color: none;
            &:after{
                display: none !important;
            }
            &:before{
                display: none !important;
            }
            .logo{
                background: url(../img/logo.png) no-repeat -18px -42px;
                width: 117px !important;
                height: 31px !important;
            }
        }
    }
}
.nav{
    ul{
        display: flex;
        flex-wrap: wrap;
        li{
            margin-right: 40px;
            a{
                display: block;
                text-decoration: none;
                font-size: 18px;
                color: #444444;
                @include transition;
                position: relative;
                &:after{
                    @include after;
                    position: absolute;
                    bottom: -10px;
                    left: 0;
                    width: 20px;
                    height:2px;
                    background-color: #ff9720;
                    @include transition;
                    opacity:0;
                }
                &:hover:after{
                    opacity:1;
                    @include transition;
                }
            }
        }
        li.active a:after{
            @include after;
            position: absolute;
            bottom: -10px;
            left: 0;
            width: 20px;
            height:2px;
            background-color: #ff9720;
            @include transition;
            opacity:1;
        }
    }
}
a.authorization{
    display: block;
    text-decoration: none;
    text-align: center;
    font-size: 16px;
    color: #444444;
    background: url(../img/icon1.png) no-repeat 32px 50% #fff;
    padding-left: 16px;
    border: 1px solid #FF8E00;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    width: 165px;
    height:36px;
    line-height: 34px;
    @include transition;
    &:hover{
        background: url(../img/icon1-1.png) no-repeat 32px 50% #FF8E00;
        color: #fff;
    }
}
.btn-nav{
    display: none;
    cursor: pointer;
    width: 30px;
    height: 25px;
    margin-left: 15px;
    @include transition;
    span{
        width: 30px;
        height: 25px;
        @include transition;
        display: block;
        b{
            border-bottom: 3px solid #4A413E;
            display: block;
            width: 30px;
            @include transition;
            margin-bottom: 8px;
            &:last-child{
                margin: 0;
            }
        }
    }
}
.btn-nav2{
    @include transition;
    span{
        @include transition;
        b{
            -webkit-transform:  rotate(45deg);
            -moz-transform:  rotate(45deg);
            -ms-transform:  rotate(45deg);
            -o-transform:  rotate(45deg);
            transform:  rotate(45deg);
            position: relative;
            @include transition;
            top:10px;
            &:nth-child(2) {
                -webkit-transform:  rotate(135deg);
                -moz-transform:  rotate(135deg);
                -ms-transform:  rotate(135deg);
                -o-transform:  rotate(135deg);
                transform:  rotate(135deg);
                top: -1px;
            }
            &:last-child{
                display: none;
            }
        }
    }
}
.banner{
    background: url(../img/bg.jpg) no-repeat 50% 0;
    height:540px;
    overflow: hidden;
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    position: relative;
    &:after{
        @include after;
        position: absolute;
        bottom: -33px;
        left: 50%;
        margin-left: -595px;
        background: url(../img/img1.png) no-repeat;
        width: 202px;
        height:311px;
    }
    &:before{
        @include after;
        position: absolute;
        bottom: -28px;
        right: 50%;
        margin-right: -806px;
        background: url(../img/img2.png) no-repeat;
        width: 573px;
        height:475px;
    }
    .container{
        position: relative;
        z-index:2;
        padding-top: 108px;
        span{
            display: block;
            font-family: 'FuturaPTBold';
            color: #463f3d;
            font-size: 60px;
            margin-bottom: 8px;
            text-align: center;
        }
        strong{
            display: block;
            font-family: 'FuturaPTBook';
            color: #181818;
            font-size: 30px;
            margin-bottom: 26px;
            text-align: center;
        }
        ul{
            max-width: 750px;
            margin: 0 auto;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            li{
                background: url(../img/icon2.png) no-repeat;
                padding-top: 14px;
                padding-left: 22px;
                &:nth-child(odd) {
                    margin-top: 50px;
                }
                b{
                    display: block;
                    color: #463f3d;
                    font-size: 30px;
                    margin-bottom: 5px;
                    font-family: 'FuturaPTBold';
                }
                p{
                    display: block;
                    color: #444444;
                    font-size: 16px;
                    font-family: 'FuturaPTBook';
                }
            }
        }
    }
}
.btn{
    display: block;
    text-decoration: none;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    color: #ffffff;
    font-size: 22px;
    font-family: 'FuturaPTBook';
    text-align: center;
    @include transition;
    width: 200px;
    height:46px;
    background-color: #ff9720;
    &:hover{
        @include transition;
        background-color: #ffa53d;
    }
}
.form-order{
    max-width: 780px;
    margin: -127px auto 63px;
    padding: 0 15px;
    position: relative;
    z-index:2;
    .form-order-container{
        background-color: #fff;
        border-top: 4px solid #454645;
        -webkit-box-shadow: 0 0 75px rgba(68,68,68, 0.25);
        -moz-box-shadow: 0 0 75px rgba(68,68,68, 0.25);
        box-shadow: 0 0 75px rgba(68,68,68, 0.25);
        padding: 26px 40px 27px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
        input[type="text"] {
            display: block;
            width: 100%;
            border: 1px solid #454645;
            -webkit-border-radius: 5px;
            -moz-border-radius: 5px;
            border-radius: 5px;
            font-size: 16px;
            color: rgba(68,68,68, 0.9);
            -webkit-color: rgba(68,68,68, 0.9);
            -moz-color: rgba(68,68,68, 0.9);
            font-family: 'FuturaPTLight';
            margin-bottom: 15px;
            padding: 0 14px;
            height:46px;
            background-color: #fff;
        }
        textarea{
            display: block;
            width: 100%;
            background-color: #fff;
            border: 1px solid #454645;
            -webkit-border-radius: 5px;
            -moz-border-radius: 5px;
            border-radius: 5px;
            text-align: left;
            font-size: 16px;
