            height:135px;
        }
        .row{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            input[type="text"] {
                width: 49%;
            }
        }
        input[type="submit"] {
            cursor: pointer;
            border: none;
            display: block;
            text-decoration: none;
            -webkit-border-radius: 5px;
            -moz-border-radius: 5px;
            border-radius: 5px;
            color: #ffffff;
            font-size: 22px;
            font-family: 'FuturaPTBook';
            text-align: center;
            @include transition;
            width: 39%;
            height:46px;
